<template>
  <div class="wrap">
    <h1>welcome,{{ user }}</h1>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-card>
          <div slot="header">用户信息</div>
          <div>
            <dl>
              <dt>username:</dt>
              <dt>{{ user }}</dt>
              <dt>权限</dt>
              <dt>{{ roles }}</dt>
              <dt>所属班级</dt>
              <dt>{{ clazz }}</dt>
            </dl>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12" class="shortcuts">
        <el-card>
          <div slot="header">快捷操作</div>
          <div class="content">
            <dl>
              <dt>
                <a href="#" @click="getClass">查看班级</a>
              </dt>
              <dt>
                <a href="#" @click="logout">退出登录</a>
              </dt>
            </dl>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
//import { getToken } from "@/util/auth";
import store from "@/store/index";
export default {
  name: "index",
  data() {
    return {
      user: store.getters.name,
      classes: [{ id: -2, name: "无" }, ...store.getters.classes]
      //roles: [...store.getters.roles]
    };
  },

  computed: {
    roles: function() {
      return this.$store.getters.roles.map(item => {
        return item.role_name;
      });
    },
    clazz: function() {
      let clazzId = this.$store.getters.clazz;
      for (const i of this.classes) {
        if (i.id == clazzId) {
          return i.name;
        }
      }
      return false;
    }
  },
  methods: {
    logout() {
      this.$store.dispatch("user/logout");
      this.$router.push({ path: "/login" });
    },
    getClass() {
      this.$router.push({ name: "student" });
    }
  }
};
</script>
<style lang="scss" scoped>
@media (min-width: 375px) and (max-width: 768px){
  .shortcuts{
    margin-top: 20px;
  }
}
.el-card {
  a {
    text-decoration: none;
    color: rgb(64, 158, 255);
  }
}
</style>